.main_ItemCard {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin: 1rem;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    border: 1px gray;
    border-radius: .5rem;
    padding: 1rem;
    box-shadow: rgb(136, 136, 136) 1px 1px 5px;
    cursor: pointer;
}

.ItemCard_info_data{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.ItemCard_info{
    display: flex;
    gap: 1.5rem;
}

.main_ItemCard:hover {
    box-shadow: 1px 1px 10px #888888;
  }
